.footer {
  background-color: $mainGreen;
  width: 100%;
  height: 30px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 5px;
  position: fixed;
  bottom: 0;

  &-text {
    color: white;
    font-size: 14px;
    &-span {
      font-size: 14px;
      text-transform: capitalize;
    }
  }
}
